___CSS_LOADER_EXPORT___.push([module.id, `*, *::before, *::after {
    box-sizing: border-box;
  }
  
  * {
    margin: 0;
  }
  
  body {
    line-height: 1.5;
    -webkit-font-smoothing: antialiased;
  }
  
  img, picture, video, canvas, svg {
    display: block;
    max-width: 100%;
  }
  
  input, button, textarea, select {
    font: inherit;
  }
  
  p, h1, h2, h3, h4, h5, h6 {
    overflow-wrap: break-word;
  }
  
  p {
    text-wrap: pretty;
    padding: 0px;
    margin: 0px;
  }
  h1, h2, h3, h4, h5, h6 {
    text-wrap: balance;
  }
  
  #root, #__next {
    isolation: isolate;
  }

  html {
    height: 100vh;
  }

  body {
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .gameArea {
    height: 420px;
    width: 800px;
    /* background-color: blue; */
    display: grid;
    grid-template-rows: 30px 1fr;
    grid-template-columns: repeat(2, 1fr);
    gap: 10px;
  }

  .gameArea > * {
    /* border: 1px solid black; */
    /* background-color: #0c0c0d; */
    display: grid;
    grid-template-columns: repeat(10,1fr);
    grid-template-rows: repeat(10,1fr);
    /* gap: 1px; */
    /* padding: 2px; */
    cursor: pointer;
  }

  .player1 .tile, .player2 .tile {
    /* background-color: blue; */
    border: 0.5px solid #0c0c0d;
  }

  .player1_name, .player2_name {
    /* background-color: blue; */
    display: flex;
    color: black;
    font-size: 24px;
    font-family: Helvetica, Arial, sans-serif;
    padding: 0px;
    justify-content: center;
    align-items: center;
  }`, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":"AAAA;IACI,sBAAsB;EACxB;;EAEA;IACE,SAAS;EACX;;EAEA;IACE,gBAAgB;IAChB,mCAAmC;EACrC;;EAEA;IACE,cAAc;IACd,eAAe;EACjB;;EAEA;IACE,aAAa;EACf;;EAEA;IACE,yBAAyB;EAC3B;;EAEA;IACE,iBAAiB;IACjB,YAAY;IACZ,WAAW;EACb;EACA;IACE,kBAAkB;EACpB;;EAEA;IACE,kBAAkB;EACpB;;EAEA;IACE,aAAa;EACf;;EAEA;IACE,YAAY;IACZ,aAAa;IACb,uBAAuB;IACvB,mBAAmB;EACrB;;EAEA;IACE,aAAa;IACb,YAAY;IACZ,4BAA4B;IAC5B,aAAa;IACb,4BAA4B;IAC5B,qCAAqC;IACrC,SAAS;EACX;;EAEA;IACE,6BAA6B;IAC7B,+BAA+B;IAC/B,aAAa;IACb,qCAAqC;IACrC,kCAAkC;IAClC,cAAc;IACd,kBAAkB;IAClB,eAAe;EACjB;;EAEA;IACE,4BAA4B;IAC5B,2BAA2B;EAC7B;;EAEA;IACE,4BAA4B;IAC5B,aAAa;IACb,YAAY;IACZ,eAAe;IACf,yCAAyC;IACzC,YAAY;IACZ,uBAAuB;IACvB,mBAAmB;EACrB","sourcesContent":["*, *::before, *::after {\n    box-sizing: border-box;\n  }\n  \n  * {\n    margin: 0;\n  }\n  \n  body {\n    line-height: 1.5;\n    -webkit-font-smoothing: antialiased;\n  }\n  \n  img, picture, video, canvas, svg {\n    display: block;\n    max-width: 100%;\n  }\n  \n  input, button, textarea, select {\n    font: inherit;\n  }\n  \n  p, h1, h2, h3, h4, h5, h6 {\n    overflow-wrap: break-word;\n  }\n  \n  p {\n    text-wrap: pretty;\n    padding: 0px;\n    margin: 0px;\n  }\n  h1, h2, h3, h4, h5, h6 {\n    text-wrap: balance;\n  }\n  \n  #root, #__next {\n    isolation: isolate;\n  }\n\n  html {\n    height: 100vh;\n  }\n\n  body {\n    height: 100%;\n    display: flex;\n    justify-content: center;\n    align-items: center;\n  }\n\n  .gameArea {\n    height: 420px;\n    width: 800px;\n    /* background-color: blue; */\n    display: grid;\n    grid-template-rows: 30px 1fr;\n    grid-template-columns: repeat(2, 1fr);\n    gap: 10px;\n  }\n\n  .gameArea > * {\n    /* border: 1px solid black; */\n    /* background-color: #0c0c0d; */\n    display: grid;\n    grid-template-columns: repeat(10,1fr);\n    grid-template-rows: repeat(10,1fr);\n    /* gap: 1px; */\n    /* padding: 2px; */\n    cursor: pointer;\n  }\n\n  